body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  overflow: hidden;
  text-align: center;
}

.GameArea {
  background-image: url(/typing-game/static/media/background4.f0985cf0.jpg);
  background-repeat: repeat;
  /* background-size: cover; */
  position: absolute;
  width: 100%;
  height: 100%;
}

.Type {
  position: absolute;
  margin-left: -7.5vw;
  top: 50%;
  left:50%;
  text-align: center;
}

.TypeTextBox {
  width:15vw;
}

.Score {
  position: absolute;
  font-size: 2vw;
  top: 1vw;
  left: 85vw;
  z-index: 2;
  color: white;
  -webkit-text-emphasis: bold;
          text-emphasis: bold;
}

.GameOver {
  position: absolute;
  z-index: 7;
  top: 8vw;
  left: 35vw;
  font-size: 5vw;
  color: red;
}

.Player {
  position: absolute;
  top: 40%;
  left: 48%;
  background-image: url(/typing-game/static/media/character-left.8f00f941.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 5vw;
  width: 5vw;
}

.MonsterContainer {
  position: absolute;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 3vw;
  height: 5vw;
}

.Monster {
  max-width: 5vw;
  max-height: 5vw;
  z-index: 3;
  background-size: contain;
  background-repeat: no-repeat;
}

.MonsterName {
  z-index: 4;
  color: white;
  background-color: black;
  margin: 0;
  font-size: 1em;
  text-align: center;
}
